.elementor-flip-box {
    height: 280px;
    position: relative;
    -webkit-perspective: 1000px;
    perspective: 1000px;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
}

@media (max-device-width: 1024px) {
    .elementor-flip-box {
        cursor: pointer;
    }
}

.elementor-flip-box__front {
    background-color: #1abc9c;
}

.elementor-flip-box__back {
    background-color: #4054b2;
    display: block;
}

.elementor-flip-box__layer {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transition: all .6s ease-in-out;
    -o-transition: all .6s ease-in-out;
    transition: all .6s ease-in-out;
}

.elementor-flip-box__layer__overlay {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: stretch;
    -webkit-align-items: stretch;
    -ms-flex-align: stretch;
    align-items: stretch;
    text-align: center;
    padding: 35px;
}

.elementor-flip-box__layer__title {
    font-size: 21px;
}

.elementor-flip-box__layer__description {
    font-size: 14px;
    color: #fff;
}

.elementor-flip-box__layer__title {
    color: #fff;
}

.elementor-flip-box__layer__description:not(:last-child) {
    margin: 0 0 20px 0;
    padding: 0;
}

.elementor-flip-box__layer__title {
    &:not(:last-child) {
        margin: 0 0 20px 0;
        padding: 0;
    }
    line-height: 1;
    font-weight: 600;
}

.elementor-flip-box {
    .elementor-icon, .elementor-view-framed .elementor-icon, .elementor-view-stacked .elementor-icon {
        color: #fff;
    }
    .elementor-view-framed .elementor-icon {
        border-color: #fff;
    }
    .elementor-view-stacked .elementor-icon {
        background-color: rgba(0, 0, 0, 0.5);
    }
}

.elementor-flip-box__button.elementor-button {
    cursor: pointer;
    -webkit-align-self: center;
    -ms-flex-item-align: center;
    align-self: center;
    margin-left: auto;
    margin-right: auto;
    border: solid 2px white;
    color: white;
    background: transparent;
    &:hover {
        background: transparent;
        text-decoration: none;
    }
}

.elementor-flip-box__image {
    margin: 0 0 20px 0;
    display: inline-block;
    width: 100%;
    img {
        width: 50%;
    }
}

.elementor-flip-box .elementor-icon-wrapper {
    margin-bottom: 20px;
}

.elementor-flip-box--3d {
    .elementor-flip-box__layer__inner {
        -webkit-transform: translateZ(90px) scale(0.91);
        transform: translateZ(90px) scale(0.91);
    }
    .elementor-flip-box__layer__overlay {
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        -webkit-transform: translateZ(0.1px);
        transform: translateZ(0.1px);
    }
}

.elementor-flip-box--effect-flip {
    .elementor-flip-box {
        -webkit-perspective: 1000px;
        perspective: 1000px;
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
    }
    .elementor-flip-box__layer {
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
    }
    .elementor-flip-box__front {
        -webkit-transform: none;
        -ms-transform: none;
        transform: none;
        z-index: 1;
    }
    .elementor-flip-box:hover .elementor-flip-box__back {
        -webkit-transform: none;
        -ms-transform: none;
        transform: none;
    }
    &.elementor-flip-box--direction-right {
        .elementor-flip-box__back {
            -webkit-transform: rotateX(0) rotateY(-180deg);
            transform: rotateX(0) rotateY(-180deg);
        }
        .elementor-flip-box:hover .elementor-flip-box__front {
            -webkit-transform: rotateX(0) rotateY(180deg);
            transform: rotateX(0) rotateY(180deg);
        }
    }
    &.elementor-flip-box--direction-left {
        .elementor-flip-box__back {
            -webkit-transform: rotateX(0) rotateY(180deg);
            transform: rotateX(0) rotateY(180deg);
        }
        .elementor-flip-box:hover .elementor-flip-box__front {
            -webkit-transform: rotateX(0) rotateY(-180deg);
            transform: rotateX(0) rotateY(-180deg);
        }
    }
    &.elementor-flip-box--direction-up {
        .elementor-flip-box__back {
            -webkit-transform: rotateX(-180deg) rotateY(0);
            transform: rotateX(-180deg) rotateY(0);
        }
        .elementor-flip-box:hover .elementor-flip-box__front {
            -webkit-transform: rotateX(180deg) rotateY(0);
            transform: rotateX(180deg) rotateY(0);
        }
    }
    &.elementor-flip-box--direction-down {
        .elementor-flip-box__back {
            -webkit-transform: rotateX(180deg) rotateY(0);
            transform: rotateX(180deg) rotateY(0);
        }
        .elementor-flip-box:hover .elementor-flip-box__front {
            -webkit-transform: rotateX(-180deg) rotateY(0);
            transform: rotateX(-180deg) rotateY(0);
        }
    }
}

.elementor-flip-box--effect-push {
    .elementor-flip-box__front {
        -webkit-transform: none;
        -ms-transform: none;
        transform: none;
    }
    &.elementor-flip-box--direction-right .elementor-flip-box:hover .elementor-flip-box__front {
        -webkit-transform: translateX(100%) translateY(0);
        -ms-transform: translateX(100%) translateY(0);
        transform: translateX(100%) translateY(0);
    }
    &.elementor-flip-box--direction-left .elementor-flip-box:hover .elementor-flip-box__front {
        -webkit-transform: translateX(-100%) translateY(0);
        -ms-transform: translateX(-100%) translateY(0);
        transform: translateX(-100%) translateY(0);
    }
    &.elementor-flip-box--direction-up .elementor-flip-box:hover .elementor-flip-box__front {
        -webkit-transform: translateX(0) translateY(-100%);
        -ms-transform: translateX(0) translateY(-100%);
        transform: translateX(0) translateY(-100%);
    }
    &.elementor-flip-box--direction-down .elementor-flip-box:hover .elementor-flip-box__front {
        -webkit-transform: translateX(0) translateY(100%);
        -ms-transform: translateX(0) translateY(100%);
        transform: translateX(0) translateY(100%);
    }
}

.elementor-flip-box--effect-slide .elementor-flip-box, .elementor-flip-box--effect-push .elementor-flip-box {
    overflow: hidden;
}

.elementor-flip-box--effect-slide .elementor-flip-box:hover .elementor-flip-box__back, .elementor-flip-box--effect-push .elementor-flip-box:hover .elementor-flip-box__back {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
}

.elementor-flip-box--effect-slide.elementor-flip-box--direction-right .elementor-flip-box__back, .elementor-flip-box--effect-push.elementor-flip-box--direction-right .elementor-flip-box__back {
    -webkit-transform: translateX(-100%) translateY(0);
    -ms-transform: translateX(-100%) translateY(0);
    transform: translateX(-100%) translateY(0);
}

.elementor-flip-box--effect-slide.elementor-flip-box--direction-left .elementor-flip-box__back, .elementor-flip-box--effect-push.elementor-flip-box--direction-left .elementor-flip-box__back {
    -webkit-transform: translateX(100%) translateY(0);
    -ms-transform: translateX(100%) translateY(0);
    transform: translateX(100%) translateY(0);
}

.elementor-flip-box--effect-slide.elementor-flip-box--direction-up .elementor-flip-box__back, .elementor-flip-box--effect-push.elementor-flip-box--direction-up .elementor-flip-box__back {
    -webkit-transform: translateX(0) translateY(100%);
    -ms-transform: translateX(0) translateY(100%);
    transform: translateX(0) translateY(100%);
}

.elementor-flip-box--effect-slide.elementor-flip-box--direction-down .elementor-flip-box__back, .elementor-flip-box--effect-push.elementor-flip-box--direction-down .elementor-flip-box__back {
    -webkit-transform: translateX(0) translateY(-100%);
    -ms-transform: translateX(0) translateY(-100%);
    transform: translateX(0) translateY(-100%);
}

.elementor-flip-box--effect-zoom-out .elementor-flip-box {
    .elementor-flip-box__front {
        -webkit-transition: opacity 0.35s, width 0.1ms, -webkit-transform 0.7s;
        transition: opacity 0.35s, width 0.1ms, -webkit-transform 0.7s;
        -o-transition: transform 0.7s, opacity 0.35s, width 0.1ms;
        transition: transform 0.7s, opacity 0.35s, width 0.1ms;
        transition: transform 0.7s, opacity 0.35s, width 0.1ms, -webkit-transform 0.7s;
        opacity: 1;
        -webkit-transform: scale(1);
        -ms-transform: scale(1);
        transform: scale(1);
        z-index: 1;
        width: 100%;
    }
    &:hover .elementor-flip-box__front {
        width: 0;
        opacity: 0;
        -webkit-transform: scale(0.7);
        -ms-transform: scale(0.7);
        transform: scale(0.7);
        -webkit-transition: opacity 0.7s 0.1s, width 0.1ms 0.7s, -webkit-transform 0.8s;
        transition: opacity 0.7s 0.1s, width 0.1ms 0.7s, -webkit-transform 0.8s;
        -o-transition: transform 0.8s, opacity 0.7s 0.1s, width 0.1ms 0.7s;
        transition: transform 0.8s, opacity 0.7s 0.1s, width 0.1ms 0.7s;
        transition: transform 0.8s, opacity 0.7s 0.1s, width 0.1ms 0.7s, -webkit-transform 0.8s;
    }
}

.elementor-flip-box--effect-zoom-in .elementor-flip-box {
    .elementor-flip-box__back {
        -webkit-transition: opacity 0.5s 0.2s, -webkit-transform 0.7s;
        transition: opacity 0.5s 0.2s, -webkit-transform 0.7s;
        -o-transition: transform 0.7s, opacity 0.5s 0.2s;
        transition: transform 0.7s, opacity 0.5s 0.2s;
        transition: transform 0.7s, opacity 0.5s 0.2s, -webkit-transform 0.7s;
        opacity: 0;
        -webkit-transform: scale(0.7);
        -ms-transform: scale(0.7);
        transform: scale(0.7);
    }
    &:hover .elementor-flip-box__back {
        -webkit-transition: opacity 0.5s, -webkit-transform 0.7s;
        transition: opacity 0.5s, -webkit-transform 0.7s;
        -o-transition: transform 0.7s, opacity 0.5s;
        transition: transform 0.7s, opacity 0.5s;
        transition: transform 0.7s, opacity 0.5s, -webkit-transform 0.7s;
        opacity: 1;
        -webkit-transform: scale(1);
        -ms-transform: scale(1);
        transform: scale(1);
    }
}

.elementor-flip-box--effect-fade .elementor-flip-box {
    .elementor-flip-box__back {
        opacity: 0;
    }
    &:hover .elementor-flip-box__back {
        opacity: 1;
    }
}

.elementor-widget-flip-box.elementor-flip-box--flipped .elementor-widget-container {
    .elementor-flip-box__front {
        display: none;
    }
    .elementor-flip-box__back {
        -webkit-transform: none;
        -ms-transform: none;
        transform: none;
        opacity: 1;
    }
}