        padding: 13px 20px;
        &:hover, &:focus, &.highlighted {
            padding: 13px 20px;
        }
        &.current {
            background: #373a3c;
            color: #fff;
        }
        &.disabled {
            background: #55595c;
            color: #a1a6a9;
        }
    }
    ul {
        position: absolute;
        width: 12em;
        border-width: 0;
        border-style: solid;
        padding: 0;
    }
    span {
        &.scroll-up, &.scroll-down {
            position: absolute;
            display: none;
            visibility: hidden;
            overflow: hidden;
            background: #fff;
            height: 20px;
        }
        &.scroll-up-arrow {
            position: absolute;
            top: -2px;
            left: 50%;
            margin-left: -8px;
            width: 0;
            height: 0;
            overflow: hidden;
            border-width: 8px;
            border-style: dashed dashed solid dashed;
            border-color: transparent transparent #494c4f transparent;
        }
        &.scroll-down-arrow {
            position: absolute;
            top: -2px;
            left: 50%;
            margin-left: -8px;
            width: 0;
            height: 0;
            overflow: hidden;
            border-width: 8px;
            border-style: dashed dashed solid dashed;
            border-color: transparent transparent #494c4f transparent;
            top: 6px;
            border-style: solid dashed dashed dashed;
            border-color: #494c4f transparent transparent transparent;
        }
    }
}

