.elementor-widget-counter {
    &.elementor-position{
        &-top {
            .elementor-icon-counter{
                text-align: center;
            }
        }
        &-left,&-right {
            .elementor-counter {
                display: flex;
                align-items: center;
                .elementor-counter-number-prefix,
                .elementor-counter-number-suffix {
                    flex-grow: 0;
                }
            }
        }
        &-left {
            .elementor-counter {
                justify-content: flex-start;
                .elementor-counter-title {
                    text-align: left;
                }
            }
            .elementor-icon-counter {
                margin-right: 10px;
            }
        }
        &-right {
            .elementor-icon-counter {
                order: 2;
                margin-left: 10px;
            }
            .elementor-counter {
                justify-content: flex-end;
                .elementor-counter-title {
                    text-align: right;
                }
            }
        }
    }
}