//ELEMENT POST
.column-item{
    padding-left: $grid-gutter-width/2;
    padding-right: $grid-gutter-width/2;
    .post-inner{
        position: relative;
        margin-bottom: 40px;
        .post-thumbnail{
            overflow: hidden;
             
            a,img{
                @extend .d-block;
            }
            img{
                width: 100%;
                transform: scale(1);
                transition: all 1s;
            }
        }
        &:hover {
            .post-thumbnail img {
                transform: scale(1.1);
                transition: all 1s;
            }
            img {
                width: 100%;
            }
        }
        .entry-header{
            @extend .mb-0;
            margin-top: 20px;
            font-size: 12px;
            .posted-on .entry-date{
                text-transform: uppercase;
                @extend .c-body;
                
            }
            .byline{
                position: relative;
                padding-left: 20px;
                &:before {
                    content: "";
                    position: absolute;
                    top: 8px;
                    left: 10px;
                    width: 6px;
                    height: 1px;
                    background-color: #212121;
                }
            }
        }
        .byline .author {
            a {
                
                letter-spacing: 0.1em;
                font-weight: 600;
            }
        }
        
        .entry-title{
            @include font-size(18);
            margin: 5px 0 12px;
            line-height: 1.2;
            letter-spacing: -0.025em;
            a {
                 
                transition: none;
                @include transition(all 0.3s ease);
                &:hover{
                  text-decoration: underline;
                }
            }
        }
        .entry-content {
            line-height: 24px;
        }

        .post-excerpt {
            @include font-size(16);
        }
        .post-author{
            position: relative;
            display: block;
            @include font-size(14);
            font-weight: 600;
            padding-top: 44px;
            
            &:before{
                position: absolute;
                content: "";
                background: $primary;
                top: 22px;
                left: 0;
                width: 30px;
                height: 1px;
                z-index: 0;
            }
        }
        .link-more{
            @include font-size(14);
            font-weight: bold;
            margin-top: 20px;
            a{
                text-transform: uppercase;
               
                position: relative;
                @include transition(all 0.3s ease);
                &:hover {
                    color:$primary;
                }
            }
        }
        .post-date{
            @include font-size(14);
            color: #888;
            span{
                margin-right: 5px;
            }
        }
        p{
            @extend .mb-0;
        }

    }
}
.elementor-recent-post-wrapper {
    > .row { margin:0;}
    .article-post {
        display: flex;
        padding-bottom: 10px;
        padding-top:10px;
        border-bottom: 1px solid $border-color;
        .media {
            padding-right:10px;
        }
        .detail {
           
            font-weight: 400;
            span,
            span.date{
                
                font-weight: 400;
                @include font-size(12);
            }
            h3{
                margin:0; padding:0;
               
                font-weight: 400;
                @include font-size(16);
            }
        }
        &:last-child {
            border-bottom:0;
        }
    }
}