.site-header-account {
    position: relative;
    font-size: 14px;
    .label-name {
        max-width: 83px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        font-weight: 500;
    }
    .label{
            padding-left:10px;
    }
    > a {
        display: inline-block;
        position: relative;
        height: 40px;
        vertical-align: middle;
        line-height: 1.5;
        text-align: left;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        color: $black2;
    }
    li {
        padding: 5px 30px;
    }
}

.opal-login-form-ajax {
    &.loading {
        &:after {
            z-index: 10;
            position: absolute;
            top: 50%;
            margin: -15px auto 0;
            left: 0;
            right: 0;
        }
        &:before {
            content: "";
            display: block;
            @include size(50px, 50px);
            z-index: 10;
            position: absolute;
            top: 50%;
            margin: -25px auto 0;
            left: 0;
            right: 0;
            background: #fff;
            
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        }
    }
}
