 .oe-hidden {
    display: none;
 }
 .elementor-opal-slick-slider ul.products li.product.first {
 	clear: none !important;
 }
.elementor-opal-slick-slider ul.products li.product {
   	margin: 0 0px !important;
    padding: 0 5px !important;
    clear: none !important;
}
.elementor-opal-slick-slider *{-webkit-box-sizing: border-box; -moz-box-sizing: border-box;box-sizing: border-box;}
.elementor-opal-slick-slider ul.products{visibility: hidden; opacity:0;  transition:opacity 0.5s linear;  }
.elementor-opal-slick-slider  .products.slick-initialized { visibility: visible; opacity:1; }

ul.oe-list-inline {
	li {
		display: inline-block;
		margin-left: 12px;
		&:first-child{
			margin-left: 0;
		}
	}
	a{
		font-size: 95%;
		color: #888;
	}
}



.rounded-circle {
  border-radius: 50% !important;
}
 %color-primary {
 	color:$primary;
 }
 %color-secondary {
  	color: $primary;
 }

%background-color-primary {
	background: $primary;
}
%background-color-secondary {
	background: $primary;
}
%color-body {
	color: inherit;
}
$white1 : #f1f1f1 !default; 
$black1 : #999 !default; 
$black2 : #999 !default; 
$gray10: #CCC !default; 
$grid-gutter-width: 15 !default; 
$border-color : #f2f2f2 !default; 
$gray1 : #CCC !default;
$gray13 : #CCC !default;
$secondary : $primary ;
$transparent: none !default;

.mb-0 {
	margin-bottom: 0;
}
.d-block {
	width: 100%;
}
.mt-2{
	margin-top: 20px;
}
.mt-5 {
margin-top: 50px;
}

.effect-thumbnail-v4{
    @include transition(all 0.3s ease);
    &:hover{
        @include translateY(-5px);
    }
}


.effect-thumbnail{
    &:hover{
        .post-thumbnail{
            a{
                &:before{
                    visibility: visible;
                    @include opacity(1);
                }
            }
            img{
                -webkit-filter: grayscale(0);
                filter: grayscale(0);
                @include opacity(1);
            }
        }
    }
    .post-thumbnail{
        position: relative;
        a{
            @extend .d-block;
            background-image: linear-gradient(left, $primary, $gray13);
            background-image: -webkit-linear-gradient(left, $primary, $gray13);
            background-image: -o-linear-gradient(left, $primary, $gray13);
            background-image: -moz-linear-gradient(left, $primary, $gray13);
            background-image: -webkit-linear-gradient(left, $primary, $gray13);
            &:before{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                content: "";
                background: rgba(0,0,0,0.3);
                @extend .d-block;
                z-index: 1;
                @include opacity(0);
                visibility: hidden;
                @include transition(all 0.3s ease);
            }
        }
        img{
            @extend .d-block;
            -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
            @include opacity(0.2);
            @include transition(all 0.3s ease);
        }
    }
}
.effect-thumbnail-v2{
    &:hover{
        .entry-header{
            border-bottom: 4px solid $secondary;
        }
    }
    .entry-header{
        @include transition(all 0.3s ease);
        border-bottom: 4px solid $transparent;
    }
}

/***/
.elementor-dropdown {
    position: relative;
    &:hover .elementor-dropdown-menu {
        
        top: 100%;
        pointer-events: auto;
        opacity: 1;
        visibility: visible;
        
    }
}
.elementor-dropdown-menu {
      text-align: left;
        right: 0;
        top: calc(100% + 15px);
        position: absolute;
        @include transition();
        pointer-events: none;
        opacity: 0;
        z-index: 99;
        visibility: hidden;
        color: $black1;
        padding: 10px 15px;
        min-width: 250px;
        background: $white1;
        @include box-shadow( 0 .5rem 1rem rgba($black, .175) );
        &:before {
            content: '';
            width: 10px;
            height: 10px;
            position: absolute;
            background-color: $white1;
            top: -5px;
            right: 20px;
            transform: rotate(45deg);
            z-index: 1;
        }
        ul , li {
            padding: 0;
            margin:0;
            border-style: none;
            list-style: none;
        }
}