.elementor-widget-image-box {
    .elementor-image-box-title {
        padding-top: 0;
    }

    .elementor-image-box-img {
        position: relative;
        svg {
            display: block;
            margin: 0 auto;
        }

        &.testabc {
            &:before {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border: 1px dashed #aba5a5;
                border-radius: 100%;
                transform: scale(1.4);
            }
        }
    }
}